<template>
  <div class="schedule-home">
    <h1>2024年秋季课程表</h1>
    <div class="extra">
      <span>第{{ week }}周</span>
    </div>
    <table border="1">
      <thead>
        <tr>
          <th v-for="item in tableHead" :key="item">{{ item }}</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(row, rowIndex) in telTableData" :key="rowIndex">
          <td v-for="(col, colIndex) in row" :key="rowIndex + colIndex">
            {{ col }}
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script lang="ts" setup>
import dayjs from "dayjs";
import { ref, computed } from "vue";

const tableHead = ref([
  "/",
  "Monday",
  "Tuesday",
  "Wednesday",
  "Thursday",
  "Friday",
  "Saturday",
  "Sunday",
  "Times",
]);
const week = computed(() => {
  const termBeginsDay = dayjs("2024/09/02");
  const curDay = dayjs();
  return curDay.diff(termBeginsDay, "week") + 1;
});
const tableData = [
  ["第一节", "第二节", "第三节", "第四节", "选修"],
  ["有课", "有课", "有课"],
  ["", "有课", "", ""],
  ["有课", "", "有课", ""],
  ["", "有课", "有课", "有课", "选修课(单周)"],
  ["", "", "有课", "有课"],
  [],
  [],
  [
    "08:00 - 09:40",
    "10:00 - 11:40",
    "14:00 - 15.40",
    "16:00 - 17:40",
    "18:30 - 20:30",
  ],
];
const telTableData = computed(() => {
  const ans: any = [];
  tableData.forEach((headItem, i) => {
    headItem.forEach((cellItem, j) => {
      if (!ans[j]) {
        ans[j] = [];
      }
      ans[j][i] = cellItem;
    });
  });
  return ans;
});
</script>

